import Link from "next/link";
import { Menu } from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/24/solid";
import { useState, useEffect } from "react";
import { supabase } from "../lib/supabaseClient";

const genres = ["Ação", "Aventura", "RPG", "Indie", "Simulação", "Esportes"];

export default function Navbar() {
  const [allGames, setAllGames] = useState<any[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [filteredGames, setFilteredGames] = useState<any[]>([]);

  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("*");
      if (error) return console.error("Erro Supabase:", error);
      setAllGames(data || []);
    }
    fetchGames();
  }, []);

  useEffect(() => {
    if (!searchTerm) return setFilteredGames([]);
    const filtered = allGames.filter((g) =>
      g.title.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setFilteredGames(filtered);
  }, [searchTerm, allGames]);

  return (
    <nav className="w-full bg-gray-800 p-6 shadow-md fixed top-0 left-0 z-50 flex justify-between items-center h-20">
      {/* Logo */}
      <h1 className="text-3xl font-bold text-white">GamersClub</h1>

      {/* Menu e pesquisa */}
      <div className="flex items-center space-x-6 text-lg relative">

        {/* Explorar com dropdown */}
        <Menu as="div" className="relative inline-block text-left">
          <Menu.Button className="flex items-center text-gray-300 hover:text-blue-400 focus:outline-none">
            Explorar <ChevronDownIcon className="ml-1 w-5 h-5"/>
          </Menu.Button>
          <Menu.Items className="absolute mt-2 w-40 bg-gray-700 rounded-md shadow-lg focus:outline-none z-50">
            {genres.map((genre, i) => (
              <Menu.Item key={i}>
                {({ active }) => (
                  <Link
                    href={`/games?genre=${genre}`}
                    className={`block px-4 py-2 text-sm text-white ${active ? "bg-gray-600" : ""}`}
                  >
                    {genre}
                  </Link>
                )}
              </Menu.Item>
            ))}
          </Menu.Items>
        </Menu>

        {/* Barra de pesquisa lowkey */}
        <div className="relative">
          <input
            type="text"
            placeholder="Pesquisar..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="p-1 text-black w-32 rounded-l focus:outline-none"
          />
          <div className="absolute top-full left-0 w-64 max-h-64 overflow-y-auto bg-gray-700 rounded-b shadow-lg z-50">
            {filteredGames.map((game) => (
              <Link
                key={game.id}
                href={`/games/${game.id}`}
                className="block px-3 py-2 text-white hover:bg-gray-600"
                onClick={() => setSearchTerm("")} // limpa pesquisa ao clicar
              >
                {game.title}
              </Link>
            ))}
          </div>
        </div>

        {/* Outros links */}
        <Link href="/promotions" className="text-gray-300 hover:text-blue-400">Promoções</Link>
        <Link href="/novidades" className="text-gray-300 hover:text-blue-400">Novidades</Link>
        <Link href="/more" className="text-gray-300 hover:text-blue-400">Mais</Link>

        {/* Conta */}
        <Link href="/register" className="text-gray-300 hover:text-blue-400">Registrar</Link>
        <Link href="/login" className="text-gray-300 hover:text-blue-400">Login</Link>
      </div>
    </nav>
  );
}
